import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";

const columnData = [
  { id: 'fullname', numeric: true, disablePadding: true, label: '' },
  { id: 'sex', numeric: true, disablePadding: false, label: '' },
  { id: 'age', numeric: true, disablePadding: false, label: '' },
  { id: 'department', numeric: true, disablePadding: false, label: '' },
  { id: 'job', numeric: true, disablePadding: false, label: '' },
  { id: 'work_number', numeric: true, disablePadding: false, label: '' },
  { id: 'wechat', numeric: true, disablePadding: false, label: '' },
  { id: 'created_at', numeric: true, disablePadding: false, label: '' },
  { id: 'updated_at', numeric: true, disablePadding: false, label: '' },
];
const inputType = [];
@inject("usersInfoStores")
@observer 
class usersList extends React.Component {
  componentDidMount() {
    this.props.usersInfoStores.getData();
  }
  render() {
    return (
      <ListTableComponent 
        title=""
        operattext=""
        inputType={inputType} 
        headTitlesData={columnData} 
        data={this.props.usersInfoStores.user}  
      />
    );
  }
}
export default usersList;