import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";

const columnData = [
    { id: 'ip', numeric: true, disablePadding: true, label: 'IP' },
    { id: 'vlan', numeric: true, disablePadding: false, label: 'vlan' },
    { 
      id: 'ip_company', 
      numeric: true, 
      disablePadding: false, 
      label: ''
    },
    { id: 'ip_status', numeric: true, disablePadding: false, label: '' },
    { id: 'ip_lock', numeric: true, disablePadding: false, label: '' },
    { id: 'ip_note', numeric: true, disablePadding: false ,label: '' },
    { 
      id: 'ip_comproomname', 
      numeric: true, 
      disablePadding: false, 
      label: '' 
    },
    // { id: 'created_at', numeric: true, disablePadding: false, label: '' },
    // { id: 'updated_at', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, label: '' }
];
const inputType = [
  {
    field: "ip",
    label: "IP",
    type: "text",
    rule: {
      term: "edit",
      execute: "disabled",
      type: "edit"
    }
  },
  {
    field: "ip_start",
    label: "IP",
    type: "text",
    rule: {
      type: "add"
    },
    helperText: "ipIP"
  },
  {
    field: "ip_end",
    label: "IP",
    type: "text",
    rule: {
      type: "add"
    }
  },
  {
    field: "vlan",
    label: "vlan",
    type: "text"
  },
  {
    field: "ip_company",
    radioData: [
      {
        checked: true,
        value: "0",
        label: ""
      },
      {
        checked: false,
        value: "1",
        label: ""
      },
      {
        checked: false,
        value: "2",
        label: ""
      }
    ],
    model: {
      selectCode: (data) => {
        switch(data) {
          case "":
              return 0;
          case "":
              return 1;
          case "":
              return 2;
        }
      }
    },
    type: "switch"
  },
  {
    field: "ip_status",
    radioData: [
      {
        checked: true,
        value: "0",
        label: ""
      },
      {
        checked: false,
        value: "1",
        label: "IP"
      },
      {
        checked: false,
        value: "2",
        label: "IP"
      },
      {
        checked: false,
        value: "3",
        label: "IP"
      }
    ],
    model: {
      selectCode: (data) => {
        switch(data) {
          case "":
              return 0;
          case "(IP)":
              return 1;
          case "(IP)":
              return 2;
          case "(IP)":
              return 3;
        }
      }
    },
    type: "switch"
  },
  {
    field: "ip_lock",
    radioData: [
      {
        checked: true,
        value: "0",
        label: ""
      },
      {
        checked: false,
        value: "1",
        label: ""
      }
    ],
    model: {
      selectCode: (data) => {
        switch(data) {
          case "":
              return 0;
          case "":
              return 1;
        }
      }
    },
    type: "switch"
  },
  {
    field: "ip_note",
    label: "",
    type: "text"
  },
  {
    field: "ip_comproom",
    label: "",
    type: "select",
    defaultData: []
  }
];

const filterType = [
  {
    field: "ip_company",
    label: "",
    options: [
      {
        view: ""
      },
      {
        view: ""
      },
      {
        view: ""
      }
    ],
    type: "select"
  },
  {
    field: "ip_status",
    label: "",
    options: [
      {
        view: ""
      },
      {
        view: "(IP)"
      },
      {
        view: "(IP)"
      },
      {
        view: "(IP)"
      }
    ],
    type: "select"
  },
  {
    field: "ip_lock",
    label: "",
    options: [
      {
        view: ""
      },
      {
        view: ""
      }
    ],
    type: "select"
  },
  {
    field: "created_at",
    label: "",
    type: "date"
  }
];
@inject("ipsStores")
@observer 
class IpList extends React.Component {
  componentDidMount() {
    this.props.ipsStores.getData();
  }
  addData = (param,callbrak) => {
    // console.log(param);
    this.props.ipsStores.addData(param).then((state) => {
      callbrak(state);
    });
  }
  delData = (selectedData,callbrak) => {
    const {ipsStores} = this.props;
    let delIng = selectedData.map(item => ipsStores.delData(item));
    callbrak(delIng);
  }
  changeData = (param,callbrak) => {
    const {ipsStores} = this.props;
    ipsStores.changeData(param).then((state) => {
      callbrak(state);
    });
  }
  filterData = (param) => {
    const {ipsStores} = this.props;
    ipsStores.filterData(param);
  }
  render() {
    inputType[inputType.findIndex(item => item.field=="ip_comproom")].defaultData = this.props.ipsStores.comprooms.map(item => {
      return {
        value: item.roomid,
        text: item.machine_room_name
      }
    });
    return (
      <ListTableComponent 
        title="ip"
        operattext="ip"
        inputType={inputType}
        filterType={filterType} 
        headTitlesData={columnData} 
        data={this.props.ipsStores.ips}  
        addData={this.addData.bind(this)} 
        delData={this.delData.bind(this)}  
        changeData={this.changeData.bind(this)} 
        filterData={this.filterData.bind(this)}
      />
    );
  }
}
export default IpList;