import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
const columnData = [
    { id: 'name', numeric: true, disablePadding: false, label: '' },
    { id: 'role', numeric: true, disablePadding: false, label: '' },
    { id: 'username', numeric: true, disablePadding: false, label: '' },
    { id: 'created_at', numeric: true, disablePadding: false, label: '' },
    { id: 'updated_at', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false,  label: '' }
];
@inject("employeeManagementsStores")
@observer 
class EmployeeManagementList extends React.Component {
    componentDidMount() {
        this.props.employeeManagementsStores.getData();
    }
    render() {
        return (
          <ListTableComponent 
            title=""
            operattext=""
            headTitlesData={columnData} 
            data={this.props.employeeManagementsStores.employeeManagements}  
          />
        );
      }
}
export default EmployeeManagementList;