import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";

const columnData = [
    { id: 'cabinet_id', numeric: true, disablePadding: true, label: '' },
    { id: 'use_state_cn', numeric: true, disablePadding: false, label: '' },
    { id: 'machine_count', numeric: true, disablePadding: false, label: '' },
    { id: 'machine_room_name', numeric: true, disablePadding: false, label: '' },
    { id: 'use_type_cn', numeric: true, disablePadding: false ,label: '' }
];
const inputType = [
  {
    field: "cabinet_id",
    label: "",
    type: "text"
  },
  {
    field: "use_type",
    label: "",
    radioData: [
        {
          checked: true,
          value: "0",
          label: ""
        },
        {
          checked: false,
          value: "1",
          label: ""
        }
      ],
      model: {
        selectCode: (data) => {
          switch(data) {
            case "":
                return 0;
            case "":
                return 1;
            default:
                return data;
          }
        }
    },
    type: "switch"
  },
  {
    field: "note",
    label: "",
    type: "text"
  },
  {
    field: "machineroom_id",
    label: "",
    type: "select",
    defaultData: []
  }
];
@inject("cabinetsStores")
@observer 
class CabinetList extends React.Component {
  componentDidMount() {
    this.props.cabinetsStores.getData();
  }
  addData = (param,callbrak) => {
    // console.log(param);
    this.props.cabinetsStores.addData(param).then((state) => {
      callbrak(state);
    });
  }
  render() {
    inputType[inputType.findIndex(item => item.field=="machineroom_id")].defaultData = this.props.cabinetsStores.comprooms.map(item => {
      return {
        value: item.roomid,
        text: item.machine_room_name
      }
    });
    return (
      <ListTableComponent 
        title=""
        operattext=""
        inputType={inputType} 
        headTitlesData={columnData} 
        data={this.props.cabinetsStores.cabinets}  
        addData={this.addData.bind(this)}
      />
    );
  }
}
export default CabinetList;