import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import InputExpansion from "../component/dialog/inputExpansion.jsx";
import OrderShow from "../component/dialog/orderShow.jsx";
import {post} from "../tool/http.js";
import { inject,observer } from "mobx-react";
const qs = require('qs');
const columnData = [
    { id: 'client_name', numeric: true, disablePadding: true, label: '' },
    { id: 'sales_name', numeric: true, disablePadding: true, label: '' },
    { id: 'business_number', numeric: true, disablePadding: true, label: '' },
    { id: 'type', numeric: true, disablePadding: true, label: '' },
    { id: 'machine_number', numeric: true, disablePadding: true, label: '/' },
    { id: 'status', numeric: true, disablePadding: true, label: '' },
    { id: 'resource_detail_json.machineroom_name', numeric: true, disablePadding: true, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, extend: true, extendData: [
        {id: "order_number", label: "", type: "text"},
        {id: "resource_detail", label: "", type: "subordinate", subordinate: [
          {id: "machine_num", label: "", type: "text"},
          {id: "cpu", label: "CPU", type: "text"},
          {id: "memory", label: "", type: "text"},
          {id: "harddisk", label: "", type: "text"},
          {id: "bandwidth", label: "", type: "text"},
          {id: "protect", label: "", type: "text"},
          {id: "loginname", label: "", type: "text"},
          {id: "loginpass", label: "", type: "text"},
          {id: "machine_type", label: "", type: "text"},
          {id: "machine_note", label: "", type: "text"},
          {id: "cabinet_id", label: "", type: "text"}
        ]},
        {id: "money", label: "" ,type: "text"},
        {id: "length", label: "" ,type: "text"},
        {id: "start_time", label: "" ,type: "text"},
        {id: "endding_time", label: "" ,type: "text"},
        {id: "business_note", label: "" ,type: "text"}
    ],extendConfirm: {
      rule: {
        term: "business_status",
        execute: 1,
        type: "equal"
    },
      title: "",
      content: "",
      ok: (data) => {
          return new Promise((resolve,reject) => {
              post("business/enable",{
                  business_status: 3,
                  id: data.id
              }).then((res) => {
                  if(res.data.code==1) {
                      alert("");
                      resolve(res.data);
                  } else {
                      alert(res.data.msg);
                      resolve(res.data);
                  }
              }).catch(reject);
          });
      }
    }, extendUrl: {
      title: "",
      link: "/tz_admin/business/order",
      param: ["business_number","client_id","client_name",{
        field: "resource_detail_json",
        value: ["machineroom"]
      }],
      rule: {
        term: "business_status",
        execute: 2,
        type: "equal"
    }
  }, label: '' }
];
const inputType = [
  {
    field: "client_name",
    label: "",
    type: "text"
  },
  {
    field: "money",
    label: "",
    type: "text"
  },
  {
    field: "length",
    label: "",
    type: "text"
  },
  {
    field: "business_note",
    label: "",
    type: "text"
  },
  {
    field: "business_type",
    label: "",
    type: "switch",
    radioData: [
        {
            checked: true,
            value: "1",
            label: ""
        },
        {
            checked: false,
            value: "2",
            label: ""
        },
        {
            checked: false,
            value: "3",
            label: ""
        }
    ]

  },
  {
    field: "machine_number",
    label: "/",
    type: "component",
    Component: InputExpansion,
    rule: {
      term: "business_type",
      execute: [
        {
          index: "1",
          value: "machine"
        },
        {
          index: "3",
          value: "cabinet"
        },
        {
          index: "2",
          value: "machine"
        }
      ],
      type: "component"
    }
  }
];
@inject("businessStores")
@observer 
class BusinesList extends React.Component {
  componentDidMount() {
    this.props.businessStores.getData(qs.parse(location.search.substr(1)).id);
  }
  updata() {
    this.props.businessStores.getData(qs.parse(location.search.substr(1)).id);
  }
  addData = (param,callbrak) => {
    if(location.search.indexOf("?id=")!=-1&&location.search.indexOf("&")==-1) {
      param.client_id = location.search.substr(1).split("=")[1];
      param.resource_detail = JSON.stringify(param.machine_number);
      param.machine_number = param.machine_number.id;
      this.props.businessStores.addData(param).then((state) => {
        callbrak(state);
      });
    }
  
  }
  render() {
    return (
      <ListTableComponent 
        title=""
        operattext=""
        inputType={inputType} 
        headTitlesData={columnData} 
        data={this.props.businessStores.business}  
        addData={this.addData.bind(this)} 
        updata={this.updata.bind(this)}
      />
    );
  }
}
export default BusinesList;