import React from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
const qs = require('qs');
const styles = theme => ({
    listTableComponent: {
        marginTop: 0,
        borderRadius: "0 0 4px 4px",
        boxShadow: "0px 4px 5px 0px rgba(0, 0, 0, 0.1), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12)"
    }
});
const columnData = [
    { id: 'order_sn', numeric: true, disablePadding: true, label: '' },
    { id: 'customer_name', numeric: true, disablePadding: true, label: '' },
    { id: 'business_name', numeric: true, disablePadding: true, label: '' },
    { id: 'resource_type', numeric: true, disablePadding: true, label: '' },
    { id: 'order_type', numeric: true, disablePadding: true, label: '' },
    { id: 'price', numeric: true, disablePadding: true, label: '' },
    { id: 'duration', numeric: true, disablePadding: true, label: '' },
    { id: 'payable_money', numeric: true, disablePadding: true, label: '' },
    { id: 'end_time', numeric: true, disablePadding: true, label: '' },
    { id: 'pay_type', numeric: true, disablePadding: true, label: '' },
    { id: 'pay_price', numeric: true, disablePadding: true, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, extend: true, extendData: [
        {id: "business_sn", label: "", type: "text"},
        {id: "before_money", label: "", type: "text"},
        {id: "after_money", label: "" ,type: "text"},
        {id: "resource", label: "" ,type: "text"},
        {id: "serial_number", label: "" ,type: "text"},
        {id: "pay_time", label: "" ,type: "text"},
        {id: "order_status", label: "" ,type: "text"},
        {id: "order_note", label: "" ,type: "text"},
        {id: "created_at", label: "" ,type: "text"}
    ], label: '' }
];
const inputType = [
    {
        field: "resource_type",
        label: "",
        type: "switch",
        radioData: [
            {
                checked: true,
                value: 4,
                label: "IP"
            },
            {
                checked: false,
                value: 5,
                label: "CPU"
            },
            {
                checked: false,
                value: 6,
                label: ""
            },
            {
                checked: false,
                value: 7,
                label: ""
            },
            {
                checked: false,
                value: 8,
                label: ""
            },
            {
                checked: false,
                value: 9,
                label: ""
            }
        ]
    },
    {
        field: "resource",
        label: "",
        type: "select",
        defaultData: []
    }
];
@inject("ordersStores")
@observer 
class OrderList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: "all"
        };
    }
    componentDidMount() {
        this.props.ordersStores.getData({
            business_sn: qs.parse(location.search.substr(1)).business_number
        });
        this.getResourceData({
            resource_type: {
                value: 4
            }
        },"init");
        inputType[inputType.findIndex(item => item.field=="resource_type")].model = {
            getSubordinateData: this.getResourceData.bind(this)
        };
    }
    addData = (param,callbrak) => {
        console.log(param);
        param.business_sn = qs.parse(location.search.substr(1)).business_number;
        param.customer_id = qs.parse(location.search.substr(1)).client_id;
        param.customer_name = qs.parse(location.search.substr(1)).client_name;
        // this.props.ordersStores.addData(param).then((state) => {
        //     callbrak(state);
        // });
      }
    getResourceData(param,type) {
        if(param.resource_type) {
            this.props.ordersStores.getResourceData({
                resource_type: param.resource_type.value,
                machineroom: qs.parse(location.search.substr(1)).machineroom
            });
        }
    }
    handleChange = (event, value) => {
        if(value=="all") {
            this.props.ordersStores.getData({
                business_sn: qs.parse(location.search.substr(1)).business_number
            });
        } else {
            this.props.ordersStores.getData({
                business_sn: qs.parse(location.search.substr(1)).business_number,
                resource_type: value
            });
        }
        this.props.ordersStores.type = value;
        this.setState({ value });
    }
    render() {
        const {classes} = this.props;
        inputType[inputType.findIndex(item => item.field=="resource")].defaultData = this.props.ordersStores.resource.map(item => {
            return {
              value: item,
              text: item.label
            }
        });
        return [
            <Paper square>
                <Tabs
                value={this.state.value}
                indicatorColor="primary"
                textColor="primary"
                onChange={this.handleChange}
                >
                <Tab label="" value={"all"} />
                <Tab label="" value={1} />
                <Tab label="" value={2} />
                <Tab label="" value={3} />
                <Tab label="IP" value={4} />
                <Tab label="CPU" value={5} />
                <Tab label="" value={6} />
                <Tab label="" value={7} />
                <Tab label="" value={8} />
                <Tab label="" value={9} />
                </Tabs>
            </Paper>,
            <ListTableComponent 
            className={classes.listTableComponent}
            title=""
            operattext=""
            inputType={inputType} 
            headTitlesData={columnData} 
            data={this.props.ordersStores.orders}
            addData={this.addData.bind(this)} 
          />
        ];
      }
}
OrderList.propTypes = {
    classes: PropTypes.object.isRequired,
};
const OrderListComponent = (props) => {
    return <OrderList {...props} />
}
export default withStyles(styles)(OrderListComponent);
